import { ComponentProps, FC, useMemo, useState } from 'react';
import classNames from 'classnames';

// Components
import Button from '../Button';
import Icon from '../Icon';

// Styles
import './ExportButton.scss';

// Types
import { TestMetadata } from 'types';

export type ButtonProps = ComponentProps<typeof Button> & {
  testMetadata?: {
    exportButton?: TestMetadata;
    icon: TestMetadata;
  };
};

export interface ExportButtonProps extends ButtonProps {
  /* Set the export type to excel, pdf, or csv */
  exportType?: 'excel' | 'pdf' | 'csv';
}

const ExportButton: FC<ExportButtonProps> = ({
  children,
  exportType = 'excel',
  sizeType = 'small',
  isFetching,
  styleType = 'demoted',
  testMetadata,
  ...other
}) => {
  const [iconName, setIconName] = useState<string>('');
  const [displayText, setDisplayText] = useState<string>('');

  useMemo(() => {
    if (exportType === 'excel') {
      setIconName('fa-file-excel');
      setDisplayText('Export to Excel');
    } else if (exportType === 'pdf') {
      setIconName('fa-file-pdf');
      setDisplayText('Export as PDF');
    } else {
      setIconName('fa-file-download');
      setDisplayText('Export as CSV');
    }
  }, [exportType]);

  const buttonClassNames = useMemo(
    () =>
      classNames('sb-export-button', 'sb-action-button', {
        'sb-export-button_excel': exportType === 'excel',
        'sb-export-button_pdf': exportType === 'pdf',
        'sb-export-button_csv': exportType === 'csv',
      }),
    [exportType],
  );

  return (
    <Button
      className={buttonClassNames}
      disabled={isFetching}
      isFetching={isFetching}
      styleType={styleType}
      iconType={
        <Icon
          altText={displayText}
          faPackage="fas"
          iconName={iconName}
          iconType="fa"
          size={sizeType}
          title={displayText}
          {...testMetadata?.icon}
        />
      }
      sizeType={sizeType}
      {...testMetadata?.exportButton}
      {...other}
    >
      {children === undefined ? `${displayText}` : children}
    </Button>
  );
};

export default ExportButton;
